import axios from './http'
import base from './base'

const api = {
  // 获取城市数据
  getCityAll() {
    return axios.get(base.baseUrl + base.geiCity)
  },
  getHotCity() {
    return axios.get(base.baseUrl + base.getHotSuggest)
  },
  getCategories() {
    return axios.get(base.baseUrl + base.getCategories)
  },
  getHotList(page) {
    return axios.get(base.baseUrl + base.getHotList, {
      params: {
        page: page
      }
    })
  },
  getHouseDetail(id) {
    return axios.get(base.baseUrl + base.getHouseDetail, {
      params: {
        houseId: id
      }
    })
  }
}

export default api
